.footer{
    min-height: 200px;
    background-image: url('../css/noise.png');
    display: flex; 
    padding: 2em 0 0 0;
    flex-direction: column;   
}
.footer-title{
    padding: 1em;
    flex-grow: 1;
    width: 100%;
    font-size: 2em;
    color: #ddd;
    text-align: center;
}
.my-picture{
    display: flex;
    justify-content: center;
    height:7em;    
    width: 7em;    
    padding:0 50%;
    margin: 3em 0;

}

.thumbnail-container{    
    justify-content: center;
    padding: 2em 0 0 0;
}
.circled-tumbnail{        
    background:linear-gradient(45deg, rgba(17,150,145) 0%, rgba(17,200,145,1) 100%);
    border-radius: 50%;  
    height:7em;    
    width: 7em;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-box-shadow: 0px 3px 10px -3px rgba(200,200,200,1);
    -moz-box-shadow: 0px 3px 10px -3px rgba(200,200,200,1);
    box-shadow: 0px 3px 10px -3px rgba(200,200,200,1);    
    margin: 2em 0;
}
.thumbnail-container .cell{
    justify-content: center;
    display: flex;

}
.inner-circle{
    background-image: url('../css/noise.png');
    border-radius: 50%;  
    height:90%;    
    width: 90%;
}

.contact{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.social-media-container
{
    justify-content: center;
}
.customeTheme{
    background: linear-gradient(45deg, rgba(0,42,66,1) 0%, rgba(29,139,109,1) 100%);
    color: white;
    font-size: 2em;    
}
.__react_component_tooltip.type-dark.place-top:after{
    border-top-color: rgba(9, 74, 80,1)!important;

}

